:root {
  --clr-bg: hsl(0, 0%, 100%);
  --clr-bg-pattern: hsl(225, 100%, 98%);
  --clr-bg-card: hsl(227, 47%, 96%);
  --clr-text-400: hsl(228, 12%, 44%);
  --clr-text-500: hsl(230, 17%, 14%);
  --clr-toggle: hsl(230, 22%, 74%);
}

.darkmode:root {
  --clr-bg: hsl(230, 17%, 14%);
  --clr-bg-pattern: hsl(232, 19%, 15%);
  --clr-bg-card: hsl(228, 28%, 20%);
  --clr-text-400: hsl(228, 34%, 66%);
  --clr-text-500: hsl(0, 0%, 100%);
  --clr-toggle: linear-gradient(to right, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  transition: 0.4s ease-in-out;
}

input[type=checkbox] {
  appearance: none;
}

html {
  font-size: 62.5%;
}

main {
  font-family: "Inter", sans serif;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: -0.1rem;
}

.header__title {
  font-size: clamp(1.92rem, 4.25vw, 2.8rem);
}
.header__subtitle,
.header .switch {
  font-size: z;
}

.followers-count__num {
  font-size: 5.6rem;
}
.followers-count__num span {
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 0.5rem;
}

.overview__title {
  font-size: clamp(1.92rem, 4.25vw, 2.4rem);
}
.overview__num {
  font-size: 3.2rem;
}

.header {
  align-items: center;
  padding: 2.4rem;
  background-color: var(--clr-bg-pattern);
  position: relative;
  min-height: 16rem;
}
.header__title {
  color: var(--clr-text-500);
}
.header__subtitle {
  color: var(--clr-text-400);
}
.header__divider {
  border: none;
  border-top: 0.1rem solid var(--clr-text-400);
  margin: 2.4rem auto;
}

.switch {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.6rem;
}
.switch__label {
  color: var(--clr-text-400);
}
.switch__button {
  cursor: pointer;
  width: 4.8rem;
  height: 2.4rem;
  background-color: var(--clr-toggle);
  background-image: var(--clr-toggle);
  border-radius: 100vw;
  position: relative;
  display: flex;
  align-items: center;
}
.switch__button::before {
  content: "";
  width: 1.6rem;
  height: 1.6rem;
  background-color: var(--clr-bg);
  border-radius: 100vw;
  transform: scale(1.1);
  position: absolute;
  left: 2.8rem;
  transition: all 0.4s;
}
.switch__button:checked::before {
  left: 0.4rem;
}